*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --trns-general: 250ms cubic-bezier(.4, 0, .2, 1);
  --animate: 250ms cubic-bezier(.4, 0, .2, 1);
}

.no-scroll {
  overflow: hidden;
}

.header {
  padding-top: 24px;
}

.navigation {
  display: none;
}

.navigation a {
  position: relative;
}

.navigation a:hover:before, .navigation a:focus:before {
  transform: scaleX(1);
}

.header-nav-links {
  color: var(--main-color);
  font-size: 12px;
  font-weight: 500;
  font-family: var(--basic-font-family);
}

.header-nav-links:hover, .header-nav-links:focus {
  color: var(--btn-color);
  transition: var(--trns-general);
}

.btn-home {
  margin-right: 16px;
}

.logo-first {
  color: var(--btn-color);
  letter-spacing: -.64px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.125;
}

.logo-second {
  color: var(--main-color);
  letter-spacing: -.64px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.125;
}

.header-items {
  align-items: center;
  display: flex;
}

.header-icons {
  align-items: center;
  gap: 14px;
  margin-left: auto;
  display: flex;
}

.header-order-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

.mob-menu-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
}

.mobile-menu-icon {
  stroke: var(--main-color);
}

.header-switch, .header-switch input {
  display: none;
}

.header-slider {
  cursor: pointer;
  background-color: #cecdcd;
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 4px 4px 15px rgba(56, 52, 52, .25);
}

.header-slider:before {
  content: "";
  height: 17px;
  width: 17px;
  background-color: var(--secondary-color);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 2px;
}

.header-switch input:checked + .header-slider {
  background: linear-gradient(#9bb537, #9bb537);
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.header-switch input:checked + .header-slider:before {
  transform: translateX(26px);
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.mobile-menu {
  background-color: var(--btn-color);
  height: 100%;
  width: 200px;
  z-index: 999;
  transition: transform .3s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.mobile-menu.open {
  transform: translateX(0);
}

.mob-menu-btn-close {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  transition: transform .3s ease-in-out;
  position: absolute;
  top: 20px;
  right: 20px;
}

.mob-menu-btn-close .mob-menu-close {
  transition: transform .3s ease-in-out;
}

.mob-menu-btn-close:hover {
  transform: rotate(45deg);
}

.mob-menu-btn-close:hover .mob-menu-close {
  transform: scale(1.2);
}

.mob-menu-close-icon {
  stroke: #fff;
}

.mob-menu-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 16px;
  padding-top: 200px;
  padding-bottom: 566px;
  display: flex;
}

.header-list-item .main-page, .header-list-item {
  color: #fff;
  justify-content: center;
  font-size: 12px;
  font-weight: 500;
}

.active-header-list-item {
  color: var(--main-color);
}

.switch {
  width: 46px;
  height: 20px;
  position: absolute;
  bottom: 30px;
  left: 20px;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background-color: #cecdcd;
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 15px;
  width: 15px;
  background-color: var(--secondary-color);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 3px;
  left: 4px;
}

input:checked + .slider {
  background: var(--main-color);
}

input:checked + .slider:before {
  transform: translateX(26px);
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.nav-link-active {
  color: var(--btn-color);
}

.shopping-icon-use {
  fill: #fff;
  stroke: var(--main-color);
}

@media screen and (min-width: 768px) {
  .header {
    padding-top: 28px;
  }

  .navigation {
    display: inline;
    position: relative;
  }

  .logo-first, .logo-second {
    letter-spacing: -.72px;
