.loader {
  width: 48px;
  height: 48px;
  border: 3px solid;
  border-color: var(--btn-color) rgba(0, 0, 0, 0) #fff rgba(0, 0, 0, 0);
  box-sizing: border-box;
  border-radius: 50%;
  margin-top: 50px;
  animation: rotate 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  border: 10px solid rgba(0, 0, 0, 0);
  border-bottom-color: var(--secondary-color);
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-10px, 19px)rotate(-35deg);
}

.loader:after {
  border-color: var(--btn-color) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  transform: translate(32px, 3px)rotate(-35deg);
}

